.doc {
    /*position: relative;*/
    display: inline-block;
    width: 300px;
    margin: 10px 0 50px 20px;
    padding: 10px;
    color: #fff;
    /*text-align: center;*/
    background: #9A8B8BFF;
    background: linear-gradient(to left bottom, transparent 50%, rgba(0, 0, 0, .4) 0) no-repeat 100% 0 / 30px 30px,
    linear-gradient(-135deg, transparent 20px, #3c9604 0);
    /*filter: drop-shadow(20px 30px rgba(0, 0, 0, 0.1));*/
}

.doc::before {
    content: ' ';
    /*position: relative;*/
    top: 30px;
    right: 0;
    background: linear-gradient(to left bottom, transparent 50%, rgba(0, 0, 0, 0.1) 0) 100% 0 no-repeat;
    width: 30px;
    height: 30px;
    transform: rotate(180deg)
}

.name {
    color: blueviolet;
}

.text {
    color: aliceblue;
}

.time {

    color: midnightblue;
}